import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import MobileFriendlyOutlinedIcon from '@mui/icons-material/MobileFriendlyOutlined';
import SubscriptionsOutlinedIcon from '@mui/icons-material/SubscriptionsOutlined';
import VideoLibraryOutlinedIcon from '@mui/icons-material/VideoLibraryOutlined';
import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined';
import HistoryOutlinedIcon from '@mui/icons-material/HistoryOutlined';
import HelpOutlineOutlinedIcon from '@mui/icons-material/HelpOutlineOutlined';
import {NavLink, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { openSidebar } from '../redux/sidebarRedux';


const Sidebar = () => {

  
  const {isOpen} = useSelector(state=>state.sidebar)
 
  return (
    <>
    {
      isOpen &&
      <div className={` w-[250px] md:w-[70px] h-[100vh] bg-[#fff] p-2 sm:hidden`}>
        <div className='flex flex-col gap-2 '>
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 ' to='/' >  <HomeOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> Home</span></NavLink>
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 ' to='/shorts' >  <MobileFriendlyOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> Shorts</span></NavLink>
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 'to='/subs' >  <SubscriptionsOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> Subscriptions</span></NavLink>
        <br />
        <hr />  
        <br />
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 ' to='/library' >  <VideoLibraryOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> Library</span></NavLink>
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 ' to='/history' >  <HistoryOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> History</span></NavLink>
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 'to='/settings' >  <SettingsOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> Settings</span></NavLink>
        <NavLink className='p-2 font-thin hover:bg-[#e9e9e9] stroke-white stroke-1 'to='/help' >  <HelpOutlineOutlinedIcon fontSize='large' className='p-1' /><span className='ml-[20px] text-sm md:hidden'> Help</span></NavLink>
        <br />  
        <hr />
        <br />
        </div>
       
    </div>
  }
    </>
  )
}

export default Sidebar