import axios from "axios"
import { signInWithPopup } from "firebase/auth"
import { auth, Google } from "../firebase"
import { getMeFailure, getMeStart, getMeSuccess, loginFailure, loginStart, loginSuccess } from "../redux/userRedux"



export const login = async(dispatch) =>{
     dispatch(loginStart())
         const {user} =  await signInWithPopup(auth, Google)
         const requestOptions = {
            method: 'POST',
            body: JSON.stringify({email:user.email, name:user.displayName, profilePic:user.photoURL})
        };
         fetch('/auth/google', requestOptions).then(()=>{
             dispatch(loginSuccess())    
         }).catch((err)=>{
             dispatch(loginFailure(err))
         })
        //  await axios.post('/auth/google',
        //   {email:user.email, name:user.displayName, profilePic:user.photoURL}, 
        //   {withCredentials:true}
        //  )
        
} 

export const getMe = async(dispatch) =>{
    try{

        dispatch(getMeStart())
       const {data} =  await axios.get('/user', {withCredentials:true } )
        dispatch(getMeSuccess(data))
    }
    catch(err){
        dispatch(getMeFailure(err.response.data.error))

        
    }
} 



