import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import { format } from 'timeago.js';
import { Tooltip } from '@mui/material';
import { Link } from 'react-router-dom';

const HomeItem = ({title,thumbnail, user, views, createdAt}) => {

  return (
    <div className='w-[300px] mb-[20px] '>
        <div className='relative'>
        <span className='absolute bg-black text-white bottom-2 right-2'>11:30</span>
        <img src={thumbnail} alt={title} className='h-[180px] rounded-[10px] w-[300px] object-cover' />
        </div>
        <div>
            <div className='flex  gap-2 '>
              <Link to={`/channel/${user?._id}`}>
            <img src="pfp.jpg" className='w-[40px] h-[40px] mt-2 rounded-[50%]' alt="" />
              </Link>
            <div className='flex flex-col'>
            <span  className=' font-medium font-[Roboto] mt-2 title'>{title}</span>
            <Tooltip placement='top-start'  title={<h1 style={{fontSize:'13px'}}>{user?.name}</h1>}>
              <Link to={`/channel/${user?._id}`}>
            <span className='text-[13px] mt-[10px] text-[#5f5f5f] hover:text-[#202020] '>{user?.name}</span>
              </Link> 
            </Tooltip>
            <span className='text-[13px]  text-[#5f5f5f]'>{views} views - {format(createdAt)} </span>
            </div>
            </div>
        </div>
    </div>
  )
}

export default HomeItem