import { createSlice } from "@reduxjs/toolkit";

const sidebarSlice = createSlice({

    name:'Sidebar',
    initialState:{
      isOpen : true
        
    },
    reducers:{
        openSidebar:(state)=>{
            state.isOpen = !state.isOpen;
        },
       
    }
})

export const {openSidebar} = sidebarSlice.actions
export default sidebarSlice.reducer;
