import React, { useEffect, useState } from 'react'
import ThumbUpAltOutlinedIcon from '@mui/icons-material/ThumbUpAltOutlined';
import ThumbDownAltOutlinedIcon from '@mui/icons-material/ThumbDownAltOutlined';
import ShareOutlinedIcon from '@mui/icons-material/ShareOutlined';
import WatchItems from './WatchItems';
import {DefaultPlayer as Video} from 'react-html5video'
import 'react-html5video/dist/styles.css'
import { useLocation } from 'react-router';
import { watchVideo } from '../apiCalls/videoApis';
import { useDispatch, useSelector } from 'react-redux';
import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';

import axios from 'axios';
import { subscribeFailure, subscribeStart, subscribeSuccess } from '../redux/subscribeRedux';

const Watch = () => {
  const location = useLocation()
  const id = location.pathname.split('/')[2]
  const {video} = useSelector(state=>state.watch)
  const [videos, setVideos] = useState([])
  const { isFetching , isSubscribed} = useSelector(state=>state.subscribe)
  const dispatch = useDispatch()
  document.title = `${video?.title} - Youtube`
  useEffect(()=>{
    watchVideo(dispatch, id)
  },[dispatch, id,])


  useEffect(()=>{
    const fetchData = async()=>{
      const {data} = await axios.get('/video/videos')
      setVideos(data)
    }
    fetchData()
    }, [])
  
  
  const handleSubscribe = async()=>{
    dispatch(subscribeStart())
    try{
      const {data} = await axios.put(`/user/subscribe/${video?.user?._id}`, '', {withCredentials:true})
      dispatch(subscribeSuccess(data))
    }catch(err){
      dispatch(subscribeFailure(err))
    }
  }
  
  console.log(isSubscribed)
 const subbed = video?.user?.subscriptions.filter((s)=>s === video?.user?._id).toString()


  return (
    <div className='w-[100vw] h-[100vh] flex md:flex-col '>
        {/* Left */}
        <div className='w-[70%] md:w-[100%]  md:mx-auto'>
        <div className='video-wrapper aspect-video w-[90%] mx-auto'>
       <Video src={video?.url}
        autoPlay loop poster={video?.thumbnail  }
        > 
          <source src={video?.url} type='video/webm' />
        </Video>
        <h1 className='mt-[20px]  font-[Roboto] break-words	 font-medium	 text-[20px] md:text-[1.3rem]'>{video?.title}</h1>
        <div className='flex justify-between mt-[10px] sm:flex-col'>
        <div className='flex items-center gap-[10px]'>
          <img src={video?.user?.profilePic} className='w-[50px] md:w-[40px] aspect-square rounded-[50%] object-cover' alt="" />
          <div className='flex flex-col'>
            <span className='font-medium font-[Roboto] md:text-[14px]' >{video?.user?.name}</span>
            <span className='font-[Roboto] text-[#7a7a7a] text-[13px] md:text-[11px]'>{video?.user?.subscribers} subscribers</span>
          </div>
            {
            isSubscribed === true ||    subbed ?
           <button onClick={handleSubscribe} className='flex items-center gap-2 p-[10px] bg-[#e9e9e9] rounded-[20px] hover:bg-[#dddddd] cursor-pointer'><NotificationsNoneOutlinedIcon fontSize='small' /><span className='text-[12px]'>Subscribed</span></button>
           : isSubscribed === false || !subbed &&
           <button onClick={handleSubscribe} className='font-[Roboto] bg-black text-white px-[12px] py-[8px] rounded-[50px] text-[15px] font-medium md:text-[11px] ml-[1rem] '>Subscribe</button>
          }
           
         
      
        </div>
        <div className='flex items-center gap-3 md:gap-1 sm:mt-5'>
            <div className='flex gap-3 md:gap-1 items-center bg-[#e9e9e9] p-[10px] rounded-[20px] '><ThumbUpAltOutlinedIcon fontSize='small' className=' text-[#4d4d4d] hover:text-blue-600 cursor-pointer' /><span className='mr-[7px] text-[12px]'>{video?.likes.length}</span><ThumbDownAltOutlinedIcon fontSize='small' className='hover:text-red-600 cursor-pointer'  /></div>
          <div className='flex items-center gap-2 p-[10px] bg-[#e9e9e9] rounded-[20px] hover:bg-[#dddddd] cursor-pointer'><ShareOutlinedIcon fontSize='small' /><span className='text-[12px]'>Share</span></div>
        </div>

        </div>
        </div>
        </div>
        {/* Right */} 
        <div className='w-[450px] md:w-[90%] md:mx-auto'>
          {
            videos?.map((v)=>(
              <WatchItems key={v._id} {...v} />  
            ))
          }
          
        </div>

    </div>
  )
}

export default Watch