import axios from "axios"
import { watchFailure, watchStart, watchSuccess } from "../redux/watchRedux"
import { getDownloadURL, getStorage, ref, uploadBytesResumable } from 'firebase/storage'
import app from '../firebase'
import { async } from "@firebase/util"


export const watchVideo =async (dispatch, id)=>{
    dispatch(watchStart())
    try{
        
        const {data} = await axios.get(`/video/watch/${id}`)
        dispatch(watchSuccess(data))
    }catch(err){
        dispatch(watchFailure(err))
    }


}
export const uploadThumbnail =async (file, setthumbnailUrl, setProgress, setThumbnailSuccess)=>{
    try{
        if(file){

            const storage = getStorage  (app)
            const filename = new Date().getTime() + file.name;
            const storageRef = ref(storage, `thumbnails/${filename}`);
            const uploadTask = uploadBytesResumable(storageRef, file);
            uploadTask.on(
                "state_changed",
                (snapshot)=>{
                    const prog = Math.round(
                        (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                        )
                        setProgress(prog)
                    },
                    (error)=>console.log(error),
                    async ()=>{
                        await getDownloadURL(uploadTask.snapshot.ref).then((downloadURLs)=>{
                            setProgress(null)
                            setThumbnailSuccess(true)
                             setthumbnailUrl(downloadURLs)
                         }).catch((err)=>{
                            console.log(err)
                         })
                    }
                    )
                    
                }
    }catch(err){
        console.log(err)
    }


}
export const uploadVideo =async (file, setVideoUrl, setProgress, setVideoSuccess)=>{
    try{
        if(file){

            const storage = getStorage  (app)
            const filename = new Date().getTime() + file.name;
            const storageRef = ref(storage, `videos/${filename}`);
            const uploadTask = uploadBytesResumable(storageRef, file);
            uploadTask.on(
                "state_changed",
                (snapshot)=>{
                    const prog = Math.round(
                        (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                        )
                        setProgress(prog)
                    },
                    (error)=>console.log(error),
                    async ()=>{
                        await getDownloadURL(uploadTask.snapshot.ref).then((downloadURLs)=>{
                            
                            setProgress(null)
                            setVideoSuccess(true)
                            setVideoUrl(downloadURLs)
                        }).catch((err)=>{
                            console.log(err)
                        })
                    }
                    )
                    
                }
    }catch(err){
        console.log(err)
    }
    
    
}

export const addVideo =async (title, desc, url, thumbnail,tags)=>{
    try{
        
        await axios.post('/video', {
            title,
            desc,
            url, 
            thumbnail,
            tags
        },
        {withCredentials:true})
        console.log('Video added to your channel')
        
        
    }catch(err){
        console.log(err)
    }
}