import { Link } from "react-router-dom"


const WatchItems = ({_id, title,user, createdAt, views,thumbnail}) => {

  return (
    <Link to={`/watch/${_id}`}>
    <div className='break-all	 flex w-[85%] gap-2 mb-3 md:mt-[20px]'>
        <div className=' '>
            <img src={thumbnail} className='w-[150px] aspect-video object-cover rounded-[10px]' alt="" />
        </div>
        <div className='flex-[1]  '>
            <h1 className='title2 font-medium text-[15px] font-[Roboto] '>{title}</h1>
            <h1 className='text-[11.5px] font-[Roboto] '>Andy Khan</h1>
            <h1 className='text-[11.5px] font-[Roboto] '>22k views  -  6 months ago</h1>
        </div>

    </div>
    </Link>
  )
}

export default WatchItems