import React, { useEffect, useRef } from 'react'
import { useState } from 'react'
import { addVideo, uploadThumbnail, uploadVideo } from '../apiCalls/videoApis'
import UploadFileIcon from '@mui/icons-material/UploadFile';

const Upload = () => {
    const [thumbnail, setThumbnail] = useState(null)
    const [video, setVideo] = useState(null)
    const [progress, setProgress] = useState(null)
    const [videoSuccess, setVideoSuccess] = useState(false)
    const [thumbnailSuccess, setThumbnailSuccess] = useState(false)
    const [tags, setTags] = useState('')
    const [thumbnailUrl, setthumbnailUrl] = useState(null)
    const [videoUrl, setvideoUrl] = useState(null)
    const splitted = tags.split(' ')
    const title = useRef()
    const desc = useRef()


    useEffect(()=>{
        uploadThumbnail(thumbnail, setthumbnailUrl, setProgress, setThumbnailSuccess)
    }, [thumbnail])
    useEffect(()=>{
        uploadVideo(video, setvideoUrl, setProgress, setVideoSuccess)
    }, [ video])

    const handleUpload= (e)=>{
            e.preventDefault()
            addVideo(title.current.value, desc.current.value, videoUrl, thumbnailUrl, splitted)
    }
  return (
    <div className='w-[100vw]'>
        <div className=' min-h-[100vh] w-[70%] mx-auto p-[50px] flex flex-col gap-[20px]'>
            <h1 className='text-[20px] text-[#474747] font-bold'>Upload a video</h1>
            <input accept="video/mp4,video/x-m4v,video/*" onChange={(e)=>setVideo(e.target.files[0])} type="file" id='upload-video' className='hidden ' />
            <label className='flex items-center bg-[red] text-white p-2 rounded-[10px] w-[150px] text-[0.9rem] cursor-pointer hover:bg-[#fc5252]' htmlFor='upload-video'><UploadFileIcon />Upload a video
        </label>
       {
      progress &&  <span className='text-[red]'>Uploading {progress} % ...</span>
       }
       {
      videoSuccess &&  <span className='text-[green]'>Video Uploaded Successfully</span>
       }
            <h1 className='text-[20px] text-[#474747] font-bold'>Details</h1>
            <div className='flex gap-[5px] flex-col border-[1px] border-[#d4d4d4] rounded-[7px] p-[10px] ' >
                <span className='text-[12px] text-[#686868]'>Title</span>
                <input ref={title} type="text" placeholder='Add a title that describes your video' className='outline-none mb-[20px] text-[15px]' />
            </div>
            <div className='flex gap-[5px] flex-col border-[1px] border-[#d4d4d4] rounded-[7px] p-[10px] h-[200px]' >
                <span className='text-[12px] text-[#686868]'>Description</span>
                <input ref={desc} type="text" placeholder='Tell viewers about your video' className='outline-none mb-[20px] text-[15px]' />
            </div>
            <h1 className='text-[20px] text-[#474747] font-bold'>Thumbnail</h1>
            <label htmlFor="thumbnail-input">
                <img src={thumbnail ? URL.createObjectURL(thumbnail) : '/item.jpg' } alt="" className='rounded-[10px] w-[200px] aspect-video object-cover cursor-pointer hover:opacity-[0.8]' />
                </label>
                {
      progress &&  <span className='text-[red]'>Uploading {progress} % ...</span>
       }
       {
      thumbnailSuccess &&  <span className='text-[green]'>Thumbnail Uploaded Successfully</span>
       }
            <input onChange={(e)=> setThumbnail(e.target.files[0])} id='thumbnail-input' type="file" accept='jpg jpeg png' className='hidden' />
            <div className='flex gap-[5px] flex-col border-[1px] border-[#d4d4d4] rounded-[7px] p-[10px]' >
                <span className='text-[12px] text-[#686868]'>Tags</span>
                <input onChange={(e)=> setTags(e.target.value)} type="text" placeholder='Seperate using space' className='outline-none   text-[15px]' />
            </div>
            <button disabled={progress} onClick={handleUpload} className='disabled:bg-[gray] w-[100%] bg-[#e70404] text-white p-[10px] rounded-[15px]'>{progress ? "Uploading in progress" :'Upload'}</button>
        </div>
        
    </div>
  )
}

export default Upload