import { createSlice } from "@reduxjs/toolkit";

const watchRedux = createSlice({

    name:'Watch',
    initialState:{
        isFetching:false,
        error:false,
        video:null
    },
    reducers:{
        watchStart:(state)=>{
            state.isFetching = true;
        },
        watchSuccess:(state, action)=>{
            state.isFetching = false;
            state.isLoggedIn = true;   
            state.video = action.payload 
        },
        watchFailure:(state, action)=>{
            state.isFetching = false;
            state.error = action.payload;
        },
       
    }
})

export const {watchStart, watchSuccess, watchFailure} = watchRedux.actions
export default watchRedux.reducer;
