import React from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import VideoCallOutlinedIcon from '@mui/icons-material/VideoCallOutlined';
import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';
import { Tooltip } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import AccountCircleOutlinedIcon from '@mui/icons-material/AccountCircleOutlined';
import { useDispatch, useSelector } from 'react-redux';
import { openSidebar } from '../redux/sidebarRedux';
import { login } from '../apiCalls/userApis';
import { useRef } from 'react';
import { useState } from 'react';


const Navbar = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [searchInput, setSearchInput] = useState('');
    const {isLoggedIn, user} = useSelector(state=>state.user) 
    const handleSidebar = ()=>{
        dispatch(openSidebar())
    }
    const handleLogin = (e) =>{
        login(dispatch)
        navigate('/')
    }
    const handleSearch = (e)=>{
        e.preventDefault()
      searchInput &&  navigate(`results/${searchInput}`)
    }

  return (
    <div className='flex justify-between p-[10px] items-center mb-[20px] sm:justify-center'>

        {/* Left Section  */}
        <div className='pl-1 flex items-center gap-[20px] sm:gap-0      '>
            <div onClick={handleSidebar} className='hover:bg-[#e2e2e2] rounded-[50%] p-2 cursor-pointer sm:hidden'><MenuIcon className='text-[#616161] p-[3px] stroke-white stroke-1' fontSize='large' /></div>
        <Link to="/">
            <div className='flex'>
                <img className='w-[30px] h-[20px] self-center ' src="/youtube-logo.png" alt="youtube" />
                 <span className='font-[Oswald] text-[21px] tracking-tighter'>YouTube</span>
                 </div>
        </Link>
        </div>

        {/* Middle Section  */}

        <form  onSubmit={handleSearch} className=' flex border-2 h-10 w-[40%] rounded-[60px] items-center'>
            <div className='flex-[7]'>
                <input  onChange={(e)=>setSearchInput(e.target.value)} placeholder='Search' type="text" className='group w-[90%] focus:outline-0 pl-[10px] text-[17px]' />
            </div>
            <Tooltip title="Search">
            <div className='hover:bg-[#f1f1f1] cursor-pointer flex-[1] bg-[#f8f8f8] h-[100%] flex items-center justify-center overflow-hidden rounded-r-[60px] border-l-2 '>
            {/* <Link to={searchInput && `results/${searchInput}`}> */}
            <button  type='submit'  className=''><SearchIcon fontSize='large' className='stroke-white stroke-1 p-1 ' /></button>
            {/* </Link> */}
            </div>
            </Tooltip>
        </form>

        {/* Right Section */}
            {!isLoggedIn ? <button onClick={handleLogin} className='flex items-center gap-2 border-2 border-[#ebe8e8] px-2 py-1 rounded-[20px] font-medium font-[Roboto] text-[14px] mr-[15px] text-[#065fd4]' to="/login"><AccountCircleOutlinedIcon className='stroke-1 stroke-white' />Sign In</button> :  
        <div className='flex gap-5   items-center pr-[20px] sm:hidden'>

           <Tooltip title="Create"><Link to="/upload"><VideoCallOutlinedIcon className='stroke-white stroke-1 hover:bg-[#e2e2e2] rounded-[50%] p-[2px] cursor-pointer' fontSize='large'  /></Link></Tooltip>
        <Tooltip title="Notifications"><NotificationsNoneOutlinedIcon fontSize='large'  className=' stroke-white stroke-1 hover:bg-[#e2e2e2] rounded-[50%] p-[2px] cursor-pointer'/></Tooltip>
            <img src={user?.profilePic} alt="" className='w-[40px] object-cover rounded-[50%] cursor-pointer' />
        </div>
            }
    </div>
  )
}

export default Navbar